<template>
  <div class="hello">
    <vue-particles color="#dedede" style="position: absolute; z-index: 0;"></vue-particles>
    <div style="z-index: 1;">
      <v-avatar size="200">
        <img :src="require('../assets/myPic.jpg')" alt="avatar">
      </v-avatar>
      <h1 class="mt-3 mb-2"> Sam Ho </h1>
      <div style="font-size: 10px; color: #B1B1B1" > Great things never came from comfort zones. </div>
      <v-container style="color: #B4B4B4" class="pt-5 mt-3">
        <v-btn style="font-size: 10px" @click="$vuetify.goTo('#travel')">Travel</v-btn>
        <v-btn style="font-size: 10px" @click="$vuetify.goTo('#graduation')">Graduation</v-btn>
      </v-container>
      <div id="travel" style="width: 100%;">
        <v-layout>
          <v-flex xs0 md1 lg2></v-flex>
          <v-flex xs12 md10 lg8>
            <v-carousel style="height: auto">
              <v-carousel-item
                v-for="item in items"
              >
                <v-container fill-height justify-center>
                  <v-img
                    :src="require(`../assets/travel/${item}`)"
                    aspect-ratio="1.6"
                  ></v-img>
                </v-container>
              </v-carousel-item>
            </v-carousel>
          </v-flex>
          <v-flex xs0 md1 lg2></v-flex>
        </v-layout>
      </div>
      <div id="graduation" style="width: 100%">
        <v-layout>
          <v-flex xs0 md1 lg2></v-flex>
          <v-flex xs12 md10 lg8>
            <v-carousel>
              <v-carousel-item
                v-for="item in items"
              >
                <v-container fill-height justify-center>
                  <v-img
                    :src="require(`../assets/graduation/${item}`)"
                    aspect-ratio="1.6"
                  ></v-img>
                </v-container>
              </v-carousel-item>
            </v-carousel>
          </v-flex>
          <v-flex xs0 md1 lg2></v-flex>
        </v-layout>
      </div>
    </div>
  </div>
</template>


<script>
export default {
  name: 'HelloWorld',
  data() {
    return {
      msg: 'Welcome to Your Vue.js App',
      items: ['1.jpg', '2.jpg', '3.jpg']
    };
  },
};
</script>

<style>
#travel {
  position: absolute;
  top: 100vh;
}
#graduation{
  position: absolute;
  top: 200vh;
}
</style>







